const waterForm = document.querySelector(".form-water");
const initialAsk = document.querySelector(".initial-ask");
const btnsYesNo = document.querySelector(".btns-yes-no");

const deleteYesNo = () => {
  waterForm.removeChild(initialAsk);
};

const calculateWater = (container) => {
  console.log("calculando water"); /*  Funcion calcular agua  */

  const waterResult = document.createElement("div");
  waterResult.classList.add("water-result");
  waterResult.classList.add("result");
  waterResult.innerHTML = `
        <h4>Resultado Vía Oral</h4>
        <p>2000 mL = 2.0 L</p>
    `;

  container.appendChild(waterResult);
};

const generateWaterCalculator = () => {
  const waterCalculator = document.createElement("section");
  waterCalculator.classList.add("water-calculator");
  waterCalculator.innerHTML = `
    <div class="inputs-container">
        <div class="input">
            <label>Ultrafiltración 24 h (UF 24) mL</label>
            <input type="number" />
        </div>
        <div class="input">
            <label>Orina Total (mL)</label>
            <input type="number" />
        </div>
        <button type="submit" class="btn btn-calculate calculate-water">Calcular</button>
    </div>
    <!-- inputs-container -->
    `;
  waterForm.appendChild(waterCalculator);

  const btnCalculateWater = document.querySelector(".calculate-water");
  btnCalculateWater.addEventListener("click", (e) => {
    e.preventDefault();
    calculateWater(waterCalculator);
  });
};

const calculateDialisisTotal = (container) => {
  console.log("calculando"); /*  Funcion de calcular Dialisis  */

  const dialisisResult = document.createElement("div");
  dialisisResult.classList.add("dialisis-result");
  dialisisResult.classList.add("result");
  dialisisResult.innerHTML = `
        <h4>La Ultrafiltación 24 h es:</h4>
        <p>1000 mL = 1.0 L</p>
    `;
  container.appendChild(dialisisResult);
  generateWaterCalculator();
};

const generateDialisis = (
  container,
  insertBefore = false,
  elemntBefore = null
) => {
  const dialisis = document.createElement("div");
  dialisis.classList.add("dialisis");
  dialisis.classList.add("entry-data");

  dialisis.innerHTML = `  
    <div class="input">
        <label>Entra (Infunde) mL</label>
        <input type="number" />
    </div>
    <div class="input">
        <label>Sale (Drena) mL</label>
        <input type="number" />
    </div>
    <div class="dialisis-delta entry-result">
        <p>Diferencia</p>
        <p>-150 mL</p>
    </div>`;

  insertBefore
    ? container.insertBefore(dialisis, elemntBefore)
    : container.appendChild(dialisis);
};

const startDialisis = (container) => {
  const dialisisContainer = document.createElement("section");
  dialisisContainer.classList.add("dialisis-container");
  dialisisContainer.innerHTML = `
    <div class="dialisis yesterday-dialisis">
        <div class="input last-day">
        <label>Entra (Infunde) último día anterior (mL)</label>
        <input type="number" />
        </div>
    </div>
  `;
  container.appendChild(dialisisContainer);

  for (let count = 1; count <= 3; count++) {
    generateDialisis(dialisisContainer);
  }

  const appendBtnsDialisis = document.createElement("div");
  appendBtnsDialisis.classList.add("apend-container");
  appendBtnsDialisis.innerHTML = `    
    <button class="btn add-dialisis">Agregar Diálisis</button>
    <button type="submit" class="btn btn-calculate calculate-dialisis">Calcular Total</button>
  `;

  dialisisContainer.appendChild(appendBtnsDialisis);

  const btnAddDialisis = document.querySelector(".add-dialisis");
  btnAddDialisis.addEventListener("click", (e) => {
    e.preventDefault();
    generateDialisis(dialisisContainer, true, appendBtnsDialisis);
  });

  const btnCalculateDialisis = document.querySelector(".calculate-dialisis");
  btnCalculateDialisis.addEventListener("click", (e) => {
    e.preventDefault();
    dialisisContainer.removeChild(appendBtnsDialisis);
    calculateDialisisTotal(dialisisContainer);
  });
};

const yesAns = () => {
  deleteYesNo();
  generateWaterCalculator();
};

const noAns = () => {
  deleteYesNo();
  startDialisis(waterForm);
};

const yesOrNo = (e) => {
  e.preventDefault();
  const answer = e.target.textContent.toLowerCase();

  answer === "si" ? yesAns() : noAns();
};

btnsYesNo.addEventListener("click", (e) => {
  yesOrNo(e);
});
